#login-page {
    margin-top: 35vh;
}

#login-message {
    margin-bottom: 30px;
}

#login-button {
    background-color: rgba(241, 166, 248, 0.918);
    border: 1px groove rgb(49, 3, 49);
    border-radius: 5px;
    margin-right: 13px;
    padding: 5px;
}

#login-button:hover {
    background-color: rgba(6, 1, 7, 0.801);
    color: rgb(240, 214, 245);
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    -webkit-transition: -webkit-transform 1s;
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
}

#login-input {
    background-color: rgba(233, 214, 235, 0.918);
    border-radius: 5px;
}
.day {
    background-color: rgb(142, 205, 241);  /* default value for older browsers*/
    background: -webkit-linear-gradient(top left, rgb(142, 205, 241), rgb(233, 233, 187));
    background: linear-gradient(to bottom right, rgb(142, 205, 241), rgb(233, 233, 187));
    border: 1px groove rgb(49, 3, 49);
    border-radius: 5px;
    cursor: n-resize;
}

.day:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-transition: -webkit-transform .2s ease-out;
    transition: -webkit-transform .2s ease-out;
    transition: transform .2s ease-out;
    transition: transform .2s ease-out, -webkit-transform .2s ease-out;
}

